'use client'

import Image from 'next/image'
import { Play, Plus, Info } from 'lucide-react'

// Interface Anime Didefinisikan di sini
export interface Anime {
  mal_id: number
  title: string
  synopsis?: string
  images: {
    jpg: {
      image_url: string
      small_image_url: string
      large_image_url: string
    }
  }
}

interface HeroDetailCardProps {
  animes: Anime[]
}

export function HeroDetailCard({ animes }: HeroDetailCardProps) {
  // Ambil anime pertama sebagai fokus utama
  const featuredAnime = animes && animes.length > 0 ? animes[0] : null

  if (!featuredAnime) return null

  // Gunakan gambar URL besar (atau yang tersedia)
  const posterImage = 
    featuredAnime.images?.jpg.large_image_url || 
    featuredAnime.images?.jpg.image_url || 
    '/placeholder.jpg'

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-10 md:py-16">
        
        {/* Kontainer Utama Grid 3 Kolom */}
      <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-4 gap-8 md:gap-12 bg-gray-900 p-6 md:p-10 rounded-xl shadow-2xl">
        
        {/* KOLOM KIRI (2/3): DESKRIPSI DAN DETAIL */}
        <div className="md:col-span-2 lg:col-span-3 space-y-4 md:space-y-6">
          
            {/* Label */}
          <span className="text-sm font-semibold text-red-500 uppercase tracking-widest bg-red-500/10 px-3 py-1 rounded-full w-fit">
            ✨ Featured Title
          </span>
          
            {/* Judul */}
          <h1 className="text-4xl md:text-5xl lg:text-6xl font-extrabold text-white leading-tight">
            {featuredAnime.title}
          </h1>
          
            {/* Sinopsis */}
          <p className="text-gray-300 text-base md:text-lg line-clamp-4 max-w-3xl leading-relaxed">
            {featuredAnime.synopsis || 'Deskripsi tidak tersedia. Silakan cek detailnya untuk informasi lebih lanjut.'}
          </p>
          
            {/* Tombol Aksi */}
          <div className="flex flex-wrap gap-3 md:gap-4 pt-4">
            <button className="flex items-center gap-2 bg-red-600 text-white px-5 py-2 md:px-8 md:py-3 rounded-lg hover:bg-red-700 transition-colors font-bold shadow-md">
              <Play size={20} fill="currentColor" /> Tonton Sekarang
            </button>
            <button className="flex items-center gap-2 border border-gray-600 text-gray-300 px-5 py-2 md:px-8 md:py-3 rounded-lg hover:bg-gray-800 transition-colors">
              <Plus size={20} /> Tambahkan ke Daftar
            </button>
          </div>
        </div>
        
        {/* KOLOM KANAN (1/3): GAMBAR CARD KECIL */}
        <div className="hidden md:flex justify-end items-center">
            {/* Kontainer Card */}
            <div className="relative h-72 w-48 shadow-2xl shadow-black/80 rounded-lg overflow-hidden transition-transform duration-500 hover:scale-[1.05]">
                <Image
                    src={posterImage}
                    alt={featuredAnime.title}
                    fill
                    className="object-cover"
                    priority
                />
            </div>
        </div>
      </div>
    </div>
  )
}